h1,
h2,
h3,
p,
ul,
a {
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
  display: block;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

body {
  background-color: var(--secondary-bg-color);
  color: var(--primary-text-color);
  font-family: 'Montserrat', sans-serif;
  letter-spacing: normal;

  &.no-scroll {
    overflow: hidden;
  }

  @media screen and (min-width: 1280px) {
    background-image: url(../images/body/spoon-1.png);
    z-index: 20;
    background-repeat: no-repeat;
    background-position: -119px 550px, top;
    background-size: 178px auto;

    @media screen and (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image:
        url(../images/body/spoon-2x.png);
      background-repeat: no-repeat;
      background-position: -119px 550px, top;
      background-size: 178px auto;
    }
  }

}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section {
  padding-top: 16px;
  padding-bottom: 16px;

  @media screen and (min-width: 767.98px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 480px) {
    max-width: 480px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.subtitle {
  font-family: 'Caveat Brush';
  font-size: 24px;
  line-height: calc(30 / 24);
  text-align: center;
  color: var(--primary-text-color);

  @media screen and (min-width: 1280px) {
    font-size: 36px;
  }
}